
















































import Vue from "vue";
import Component from "vue-class-component";
import Axios from "axios";
import moment from "moment";
import * as indexApi from "../api/indexApi";

import MenuComponent from "./Menu.vue";

@Component({
  props: {},
  components: {
    "menu-component": MenuComponent
  }
})
export default class Home extends Vue {
  sysName = "医德快管理平台";
  collapsed = false;
  sysUserAvatar = "";
  sysUserName = sessionStorage.name;
  roleName = "";
  // sessionStorage.
  styleString = "230px";

  get userName(): string {
    return "admin";
  }

  get getDateFun() {
    return (
      "今天　" +
      moment(new Date()).format("YYYY/MM/DD") +
      "　星期" +
      "日一二三四五六".charAt(new Date().getDay())
    );
  }
  //退出登录
  logout() {
    this.$confirm("确认退出吗?", "提示", {
      //type: 'warning'
    })
      .then(() => {
        sessionStorage.removeItem("user");
        this.$router.replace("/login");
      })
      .catch(() => {});
  }
  updatepassword() {
    this.$router.push("/updatepassword");
  }
  //折叠导航栏
  doCollapse() {
    let dt: any = this.$refs["menuCom"];
    dt.doCollapse();
    this.collapsed = !this.collapsed;
  }

  getYdkPrescriptionStatusNum() {
    (<any>this.$refs.menuCom).getYdkPrescriptionStatusNum();
  }
updateYdkPrescriptionStatusNum(blueSignal){
// NEW_PRESCRIPTION
// NEW_ORDER
indexApi.updateYdkPrescriptionStatusNum({blueSignal:blueSignal}).then(res => {
  this.getYdkPrescriptionStatusNum()
});


}
  mounted() {
    var user = sessionStorage.getItem("user");
  }
}



